.App1 {
  /* max-width: 600px; */
  margin: 6rem auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.chat-box {
  height: 400px;
  border: 1px solid #ccc;
  border-radius: 5px;
  overflow-y: auto;
  padding: 10px;
  margin-bottom: 20px;
}

.message {
  margin-bottom: 10px;
  padding: 8px 12px;
  border-radius: 20px;
  max-width: 70%;
}

.user {
  background-color: #990011FF;
  color: white;
  align-self: flex-end;
  margin-left: auto;
}

.bot {
  background-color: #f1f1f1;
  color: black;
  align-self: flex-start;
}

.input-box {
  display: flex;
  gap: 10px;
}

input[type="text"] {
  flex-grow: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

button {
  padding: 10px 20px;
  background-color: #990011FF;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #7a000dFF;
}